@import url(https://fonts.googleapis.com/css2?family=Poiret+One&family=Work+Sans:wght@300&display=swap);
* {
  box-sizing: border-box;
}

body {
  text-align: center;
  margin: 0;
  font-family: 'Poiret One', cursive;
  padding: 0px;
  background: linear-gradient(0deg, rgba(198,193,170,1) 0%, rgba(255,255,255,1) 100%)
}

/* .container {
  display: flex;
  height: 100vh;
} */

p, button {
  font-family: 'Work Sans', sans-serif;
}

main {
  margin: auto;
  width: auto;
  min-height: calc(100vh - 212px);
  display: flex;
  padding: 10px 10px 50px 10px;
}

main .body {
  margin: auto;
}

h1::after {
  content: "";
  display: block;
  border-top: 1px solid black;
  width: 100px;
  margin: 0 auto;
  margin-top: 7px;
}

button {
  border: none;
  background: none;
  font-weight: normal;
  transition: border 1s, color .3s;
  font-size: 16px;
}

button:hover {
  cursor: pointer;
  border: none;
  color: #000;
  transition: border 1s, color .3s;
}

button:disabled {
  background: none;
  color: #959595;
  cursor: not-allowed;
}

.countdown {
  margin: 85px 0;
}

.seconds, .minutes {
  display: inline-block;
  margin: 0;
  width: 100%;
}

.minutes {
  font-size: 242px;
  line-height: 0.8;
}

.seconds {
  color: #b8b768;
  font-size: 186px;
}

.controls {
  display: flex;
  justify-content: space-between;
}

.right-controls {
  text-align: right;
}

ol {
  font-family: 'Work Sans', sans-serif;
  text-align: left;
  text-align: initial;
}

.break {
  display: none;
}

.break::after {
  content: ":";
}

footer {
  background: white;
}

.info {
  text-align: left;
  text-align: initial;
  padding: 25px;
  width: auto;
  margin: 0 auto;
}

.preview-audio {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.preview-audio img {
  height: 20px;
  margin-right: 10px;
}

.how-it-works {
  text-decoration: underline;
}


@media only screen and (min-width: 730px) {

  main {
    width: 1000px;
  }

  .seconds {
    font-size: 242px;
  }

  .countdown {
    margin: 130px 0;
  }
  
  .seconds, .minutes {
    width: 464px;
  }

  .break {
    display: inline-block;
    font-weight: bold;
    margin-right: 10px;
    text-decoration: underline;
    margin: 0;
    margin-right: 10px;
    font-size: 16px;
  }

  .info {
    width: 978px;
  }
}
